* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Verdana, Geneva, sans-serif;
  overflow: hidden;
}

.btn {
  display: inline-block;
  border: none;
  background: #d6d6d6ab;
  padding: 7px 20px;
  cursor: pointer;
  outline: none;
}

.btn:hover {
  background: #797575ab;
}

input[type="text"] {
  outline: none;
}

.Tiled-back {
  width: 100vw;
  height: 100vh;
  text-align: center;
  background: url('./resources/blue-circles.png');
  background-repeat: round;
  background-size: 50px;
  padding: 0px;
  margin: 0px;
}
