#bottomback {
  position: absolute;
  background-color: #f9e7a9c0;
  color: #292635;
  bottom: -280px;

  border-radius: 20px;
  height: 300px;
  /* animation: hide 350ms ease-in-out forwards; */
}
.horiz {
  font-size: 10pt;
  outline: none;

  margin: 0px;
  padding: 0px;
  width: 400px;
}
#bottombutton {
  position: absolute;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  background-color: #5e1516;
  border-color: #5e1516;
  color: #f9e7a9c0;
  top: 0px;
  left: 0px;
}
#bottombutton:hover {
  border-color: #f9e7a9c0;
  border-width: 2px;
}

@keyframes bottomhide {
  0% {
    transform: translate(0px, -100px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}
@keyframes bottomexpand {
  0% {
    transform: translate(0px, 0px);
  }
  100% {
    transform: translate(0px, -100px);
  }
}
