#sideback {
  position: absolute;
  background-color: #f9e7a9c0;
  color: #292635;
  left: -280px;

  border-radius: 20px;
  width: 300px;
  /* animation: hide 350ms ease-in-out forwards; */
}
.vert {
  font-size: 10pt;
  outline: none;

  margin: 0px;
  padding: 0px;
  height: 200px;
}
#sidebutton {
  width: 200px;
  height: 20px;
  position: absolute;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  background-color: #5e1516;
  border-color: #5e1516;
  color: #f9e7a9c0;
  right: -90px;
  top: 90px;
  overflow: visible;
  transform: rotate(90deg);
}
#sidebutton:hover {
  border-color: #f9e7a9c0;
  border-width: 2px;
}
/* .leftText {
  font-size: 9pt;
  overflow: visible;
  transform-origin: right top 0;
} */

@keyframes sidehide {
  0% {
    transform: translate(260px, 0px);
  }
  100% {
    transform: translate(0px, 0px);
  }
}
@keyframes sideexpand {
  0% {
    transform: translate(0px, 0px);
  }
  100% {
    transform: translate(260px, 0px);
  }
}
